.banner-inicio {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	position: relative;
	overflow: hidden;

	.img-banner {

		height: 100vh !important;
		border-radius: 5px;
		z-index: -1;
	}

	>h1 {
		font-family: $font-family-Lenu;
		color: #773282;
		position: absolute;
		font-weight: 900;
		font-size: clamp(7em, 8vw - 2em, 9em);
		z-index: 2;
		top: 45%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		font-style: normal;

	}

	>div {
		width: 35em;
		height: 25em;
		background: rgb(188, 194, 194);
		border-radius: 50%;
		-webkit-filter: blur(80px);
		filter: blur(80px);
		position: absolute;
		top: 45%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	}

	>h2 {
		font-family: $font-family-Lenu;
		position: absolute;
		color: #AC7A40;
		font-style: normal;
		z-index: 2;
		font-weight: 900;
		font-size: clamp(3em, 20vw - 1em, 3em);
		top: 55%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);

	}
}

.container-extra {
	margin: auto;
}

